    }
  },
  methods: {
    onScroll () {
            if(window.scrollY>400 && ((this.$vuetify.breakpoint.name == "xl") || (this.$vuetify.breakpoint.name == "lg"))) {
                this.drawer=true
            } else {
              this.drawer=false
            }
            
    },
    setExpand() {
      this.cardexpand=true
    }
  },
  mounted() {
